@import url(https://fonts.googleapis.com/css?family=Patua+One|Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

nav{
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  min-height: 75px;
  background: #1DA1F2;
  color: white;
  }

.nav-links{
  width: 50%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}

.navbar-button {
  text-decoration: none;
  width: 7em;
  font-size: 1.5rem;
  font-family: 'Patua One', cursive;
  margin-left: auto;
  margin-right: auto;
  border-radius: .2em;
  background-color: #1DA1F2;
  color: white;
  cursor: pointer;
}

.login-form {
  display: flex;
  flex-direction: column;
  margin: 5em auto;
  width: 30em;
  height: 12em;
  align-items: center;
  justify-content: space-between;
}

.form-button {
  height: 2em;
  width: 10em;
  font-size: 1.1rem;
  font-family: 'Patua One', cursive;
  background-color: white;
  border: 1px solid black;
  border-radius: .2em;
  background-color: #1DA1F2;
  color: white;
  margin: .1em 0;
}

.button-div {
  display: flex;
  flex-direction: column;
}

.welcome-div {
  display: flex;
  flex-direction: column;
  margin: 5em auto;
  width: 30em;
  align-items: center;
}

.welcome-text {
  font-family: 'Roboto', sans-serif;  
  font-size: 1.2rem;
}

.dashboard-div {
  display: flex;
  flex-direction: column;
  margin: 5em auto;
  width: 40em;
  height: 18em;
  border: 1px solid black;
  border-radius: 1em;
  align-items: center;
}

.unlogged-dashboard-div {
  display: flex;
  flex-direction: column;
  margin: 5em auto;
  width: 40em;
  height: 18em;
  align-items: center;
}

.dashboard-header {
  font-family: 'Patua One', cursive;
  font-size: 1.5rem;
}

.dashboard-text {
  font-family: 'Roboto', sans-serif;  
  font-size: 1.2rem;
}

#difficulty-selector {
  height: 2em;
  width: 10em;
  font-size: 1.1rem;
  font-family: 'Patua One', cursive;
  background-color: white;
  border: 1px solid black;
  border-radius: .2em;
  color: black;
  margin: .1em 0;
}


